/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  appearance: none;
  text-decoration: none;
}

button {
  background: none;
  border: none;
  color: inherit;
  font: inherit;
  cursor: pointer;
  padding: 0;
  margin: 0;
}

@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
:root {
  --color-main: #1f1f1f;
  --color-sub: #4f4f4f;
  --color-bg: #fcfcfc;
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-main: #fafafa;
    --color-sub: #6f6f6f;
    --color-bg: #121212;
  }
}
.page {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  z-index: 1;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.page::-webkit-scrollbar {
  display: none;
}
.page-inner {
  display: block;
}
.page-section {
  position: relative;
  max-width: 768px;
  margin-left: 17vw;
}
@media all and (max-width: 767px) {
  .page-section {
    margin: 0 8vw;
  }
}

.section-title {
  position: absolute;
  left: -20px;
  top: 0;
  transform: translateX(-100%);
  font-size: 14px;
  font-weight: 700;
  color: var(--color-main);
  opacity: 0.4;
}
@media all and (max-width: 767px) {
  .section-title {
    font-size: 13px;
    left: 0;
    top: -10px;
    transform: translateY(-100%);
  }
}

.sticky-wrap {
  position: relative;
  width: 100%;
  height: 200vh;
}
.sticky-inner {
  position: sticky;
  top: 0;
  height: 100vh;
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
}
.loading.off {
  display: none;
}
.loading-inner {
  position: relative;
  width: 100%;
  height: 100%;
}
@media (prefers-color-scheme: dark) {
  .loading-inner {
    background-color: var(--color-bg);
  }
}

html, body {
  padding: 0;
  margin: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

body {
  font-family: "NEXON Lv1 Gothic OTF";
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
}
@media all and (max-width: 767px) {
  body {
    font-size: 12px;
    line-height: 1.3;
  }
}

main {
  background-color: var(--color-bg);
}

#p5 {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 110vw;
  height: 110vh;
  z-index: 2;
  overflow: hidden;
  pointer-events: none;
}
@media all and (max-width: 767px) {
  #p5 {
    width: 105vw;
    height: 105vh;
  }
}
#p5 canvas {
  position: relative;
  width: 100%;
  height: 100%;
}

.section-intro {
  color: var(--color-main);
  padding-bottom: 12vw;
  margin-top: 12vw;
}
@media all and (max-width: 767px) {
  .section-intro {
    padding-bottom: 20vw;
    margin-top: 20vw;
  }
}
.section-intro .title {
  font-size: 21px;
  font-weight: 700;
  letter-spacing: 10px;
  margin-bottom: 24px;
}
@media all and (max-width: 767px) {
  .section-intro .title {
    font-size: 20px;
    letter-spacing: 6px;
    margin-bottom: 18px;
    max-width: 300px;
  }
}
.section-intro .subtitle {
  font-size: 16px;
}
@media all and (max-width: 767px) {
  .section-intro .subtitle {
    font-size: 14px;
  }
}
.section-intro .link-github {
  display: inline-block;
  margin-left: 10px;
  vertical-align: middle;
}
.section-intro .link-github svg {
  width: 20px;
}
@media all and (max-width: 767px) {
  .section-intro .link-github svg {
    width: 16px;
  }
}
.section-intro .link-github svg .color {
  fill: var(--color-main);
  transition: fill 0.4s ease-out;
}
.section-intro .link-github:hover svg .color {
  fill: var(--color-sub);
}

.section-info {
  padding-bottom: 80px;
}
.section-info .info {
  display: flex;
  position: relative;
  box-sizing: border-box;
  margin-right: 30px;
  padding-right: 20px;
  margin-bottom: 30px;
}
.section-info .info:last-child {
  margin-bottom: 0;
}
.section-info .info-list {
  display: flex;
  flex-direction: column;
}
.section-info .info .timeline {
  display: inline-block;
  font-size: 12px;
  color: var(--color-sub);
  margin-left: 8px;
  vertical-align: middle;
}
.section-info .info .name {
  color: var(--color-main);
  margin-bottom: 8px;
}
.section-info .info .tag {
  font-size: 12px;
  color: var(--color-sub);
}

.section-step {
  padding-bottom: 80px;
}
.section-step .step {
  display: flex;
  position: relative;
  box-sizing: border-box;
  margin-right: 30px;
  padding-right: 20px;
  margin-bottom: 30px;
}
.section-step .step:first-child {
  margin-bottom: 0;
}
.section-step .step:last-child {
  font-size: 110%;
}
.section-step .step:last-child .name {
  font-weight: 700;
}
.section-step .step-list {
  display: flex;
  flex-direction: column-reverse;
}
.section-step .step .timeline {
  display: block;
  font-size: 12px;
  color: var(--color-main);
  margin-bottom: 5px;
}
.section-step .step .name {
  color: var(--color-main);
  margin-bottom: 8px;
}
.section-step .step .tag {
  font-size: 12px;
  color: var(--color-sub);
}

.section-project {
  padding-bottom: 80px;
}
.section-project .section-title {
  padding-top: 4px;
}
@media all and (max-width: 767px) {
  .section-project .section-title {
    padding-top: 0;
  }
}
.section-project .project {
  display: block;
  position: relative;
  overflow: hidden;
}
.section-project .project:not(:last-of-type) {
  margin-bottom: 24px;
}
@media all and (max-width: 767px) {
  .section-project .project:not(:last-of-type) {
    margin-bottom: 18px;
  }
}
.section-project .project-list {
  display: flex;
  flex-direction: column;
}
@media all and (max-width: 767px) {
  .section-project .project-list {
    bottom: 12%;
    width: 94%;
  }
}
.section-project .project .link {
  display: inline-block;
  text-align: left;
  color: var(--color-main);
}
.section-project .project .link:hover .name::before, .section-project .project .link:focus .name::before {
  transform: translateX(0);
  transition: transform 0.4s ease-out;
}
.section-project .project .name {
  position: relative;
  display: inline-block;
  overflow: hidden;
  padding: 4px;
  margin-bottom: 4px;
}
.section-project .project .name span {
  color: var(--color-main);
}
.section-project .project .name::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateX(-101%);
  background: var(--color-main);
  width: 100%;
  height: 100%;
  transition: transform 0.2s ease-out;
  mix-blend-mode: difference;
}
.section-project .project .tags {
  color: var(--color-sub);
  padding-left: 4px;
}
.section-project .project .tags span {
  white-space: nowrap;
  margin-bottom: 2px;
  font-size: 12px;
  line-height: 1.5;
}
.section-project .project .tags span:not(:last-of-type) {
  margin-right: 4px;
}

.section-tech {
  padding-bottom: 80px;
}
.section-tech .tech {
  display: inline-block;
  position: relative;
  overflow: hidden;
  margin-right: 10px;
}
.section-tech .tech.strong {
  font-weight: 700;
  font-size: 110%;
}
.section-tech .tech span {
  display: inline-block;
  color: var(--color-main);
}

.section-play {
  margin-bottom: 12vw;
}
@media all and (max-width: 767px) {
  .section-play {
    margin-bottom: 20vw;
  }
}
.section-play .section-title {
  padding-top: 4px;
}
@media all and (max-width: 767px) {
  .section-play .section-title {
    padding-top: 0;
  }
}
.section-play .play {
  display: block;
  position: relative;
  width: 45%;
  height: 360px;
  margin-bottom: 5%;
  margin-right: 5%;
  cursor: grab;
}
.section-play .play.on {
  z-index: 1;
}
.section-play .play-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.section-play .play-inner {
  border: 1px solid var(--color-sub);
  border-radius: 20px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  color: var(--color-sub);
  display: flex;
  align-items: center;
  justify-content: center;
}
.section-play .play-canvas {
  width: 100%;
  height: 100%;
}